<template>
    <div class="detailWrap" safe-area-inset-top>
        <Back v-if="false"></Back>
        <van-swipe :autoplay="3000">
            <van-swipe-item v-for="(image, index) in images" :key="index">
                <div class="imgBox">
                    <img :src="image" style="width:100%;height: 100%;" />
                </div>

            </van-swipe-item>
        </van-swipe>
        <!-- <van-submit-bar :price="3050" button-text="提交订单" @submit="onSubmit" /> -->
        <van-action-bar>
            <van-action-bar-icon icon="shop-o" text="店铺" />
            <van-action-bar-icon icon="chat-o" text="客服" :dot="isDot" />
            <van-action-bar-icon :icon="sc[scState].icon" :text="sc[scState].text" :color="sc[scState].color" @click="scHandle"/>
            <van-action-bar-button type="warning" text="加入购物车" />
            <van-action-bar-button type="danger" text="立即购买" />
        </van-action-bar> 
    </div>
</template>
<script>
import sp1 from './images/sp1.webp';
import sp2 from './images/sp2.webp';
import sp3 from './images/sp3.webp';
import sp4 from './images/sp4.webp';
import Back from '@/components/back-icon';
export default {
    name: 'DuoBaoGeDetail',
    components: {
        Back
    },
    mixins: [],
    props: {

    },
    data() {
        return {
            images: [sp1, sp2, sp3, sp4],
            isDot:false,
            scState:0,//1已收藏 0未收藏
            sc:[{
                icon:'star-o',
                text:'收藏',
                color:'',//#ff5000 收藏之后的颜色
            },{
                icon:'star',
                text:'已收藏',
                color:'#ff5000',//#ff5000 收藏之后的颜色
            }],
        }
    },
    computed: {

    },
    watch: {

    },
    mounted() {

    },
    methods: {
        scHandle(){
            if(this.scState==0){
                this.scState=1;
            }else{
                this.scState=0;
            }
        },
    }
};
</script>
<style lang='less' scoped>
.detailWrap {
    .imgBox {
        width: 100%;
        text-align: center;
        background-color: rgba(225, 226, 225, .1);
        height: 350px
    }
}
</style>